import { WeekDay } from "@angular/common";

export interface StartEndDateTime {
  readonly startDateTime: Date;
  readonly endDateTime: Date;
  readonly startEndDateTimeRangeTstzRangeText: string;
}

export interface EventBookingDateTimeRange {
  readonly eventStartEndDateTime: StartEndDateTime;
  readonly bookingStartEndDateTime: StartEndDateTime;
}
// tslint:disable: no-magic-numbers
/**
 * Constant in-line closest to compile time
 */
const enum RecurringDayMilliseconds {
  Daily = 86400000,
  Weekly = Daily * 7
}
// tslint:enable: no-magic-numbers

const range = (start: number, stop: number, step: number) =>
  Array.from({ length: (stop - start) / step + 1 }, (_: unknown, i: number) => start + i * step);

/**
 * Increment in milliseconds. Default to 7 days.
 */
export function getRecurringStartEndDateTimes(
  startDateTimeToRecur: Date,
  endDateTimeToRecur: Date, // Can get endDateTime straight or from an "eventDuration" field
  // Postgres intricacies? safe approach is store all three. DST? UTC time should be okay. But non-existent times?
  // Database conventions generally see more useful data indexed by DateTimes. Can derive duration if required.
  // Less important statics on duration average etc.
  // More important in front end or logic rules based on DateTime points.
  numEvents: number = 10,
  incrementMs: number = RecurringDayMilliseconds.Weekly
): StartEndDateTime[] {
  const arrayLikeDefaultBlankLengthSize: ArrayLike<unknown> = { length: numEvents };

  const startEndDateTimes: StartEndDateTime[] = Array.from<unknown, StartEndDateTime>(
    arrayLikeDefaultBlankLengthSize,
    (value: unknown, keyIndex: number) => {
      // Pattern increment further for each of the recurring events to get to the next event.
      const multiplier: number = keyIndex;
      const startDateTime: Date = new Date(startDateTimeToRecur.getTime() + incrementMs * multiplier);
      const endDateTime: Date = new Date(endDateTimeToRecur.getTime() + incrementMs * multiplier);
      const startEndDateTimeRangeTstzRangeText: string = getPostgresTimestamptzTstzRange(startDateTime, endDateTime);

      return { startDateTime, endDateTime, startEndDateTimeRangeTstzRangeText };
    }
  );

  return startEndDateTimes;
}

/**
 * Increment in milliseconds. Default to 7 days.
 *
 *
 * Can get endDateTime straight or from an "eventDuration" field
 *
 * Postgres intricacies? safe approach is store all three. DST? UTC time should be okay. But non-existent times?
 *
 * Database conventions generally see more useful data indexed by DateTimes. Can derive duration if required.
 * Less important statics on duration average etc.
 * More important in front end or logic rules based on DateTime points.
 */
export function getRecurringEventBookingDateTime(
  eventStartDateTimeToRecur: Date,
  eventEndDateTimeToRecur: Date,
  bookingStartDateTimeToRecur: Date,
  bookingEndDateTimeToRecur: Date,
  numEvents: number = 10,
  incrementMs: number = RecurringDayMilliseconds.Weekly
): EventBookingDateTimeRange[] {
  // JavaScript arrays have some special length and [] behaviour. Setting length modifies how the array is used (e.g. length 0 = clear)
  const arrayLikeDefaultBlankLengthSize: ArrayLike<unknown> = { length: numEvents };

  const eventBookingStartEndDateTimes: EventBookingDateTimeRange[] = Array.from<unknown, EventBookingDateTimeRange>(
    arrayLikeDefaultBlankLengthSize,
    (value: unknown, keyIndex: number) => {
      // Pattern increment further for each of the recurring events to get to the next event.
      const multiplier: number = keyIndex;
      const eventStartEpochMs: number = eventStartDateTimeToRecur.getTime();
      const eventEndEpochMs: number = eventEndDateTimeToRecur.getTime();
      const bookingStartEpochMs: number = bookingStartDateTimeToRecur.getTime();
      const bookingEndEpochMs: number = bookingEndDateTimeToRecur.getTime();

      const incrementForNextRecurrence: number = incrementMs * multiplier;

      const nextStartEpochRecurringCalculated: number = eventStartEpochMs + incrementForNextRecurrence;
      const nextEndEpochRecurringCalculated: number = eventEndEpochMs + incrementForNextRecurrence;

      const nextBookingStartEpochRecurringCalculated: number = bookingStartEpochMs + incrementForNextRecurrence;
      const nextBookingEndEpochRecurringCalculated: number = bookingEndEpochMs + incrementForNextRecurrence;

      const eventDateTimeRange: StartEndDateTime = getEventDateTime(
        nextStartEpochRecurringCalculated,
        nextEndEpochRecurringCalculated
      );
      const bookingAvailabilityDateTimeRange: StartEndDateTime = getEventDateTime(
        nextBookingStartEpochRecurringCalculated,
        nextBookingEndEpochRecurringCalculated
      );

      const eventBookingDateTime: EventBookingDateTimeRange = {
        eventStartEndDateTime: eventDateTimeRange,
        bookingStartEndDateTime: bookingAvailabilityDateTimeRange
      };

      return eventBookingDateTime;
    }
  );

  return eventBookingStartEndDateTimes;
}

export function getEventDateTime(startEpochMilliseconds: number, endEpochMilliseconds: number): StartEndDateTime {
  const startDateTime: Date = new Date(startEpochMilliseconds);
  const endDateTime: Date = new Date(endEpochMilliseconds);
  const startEndDateTimeRangeTstzRangeText: string = getPostgresTimestamptzTstzRange(startDateTime, endDateTime);

  return { startDateTime, endDateTime, startEndDateTimeRangeTstzRangeText };
}
/**
 * Gets date incremented by days
 * @param date DateTime e.g. new Date(). Time part matters as millisecond resolution
 * @param numDays number of days to add by.
 * @returns new Date constructed incremented by days added by the millisecond conversion.
 */

export function getDateIncrementedByDays(numDays: number, date: Date = new Date()): Date {
  const timeTotalledMilliseconds: number = new Date().getTime() + getMillisecondsFromDays(numDays);

  return new Date(timeTotalledMilliseconds);
}
/**
 * Gets date incremented by seconds
 * @param date DateTime e.g. new Date(). Time part matters as millisecond resolution
 * @param numSeconds the number of seconds to increment by. E.g. for expirySeconds resolution.
 * @returns new Date constructed incremented by days added by the millisecond conversion.
 */
export function getDateIncrementedBySeconds(numSeconds: number, date: Date = new Date()): Date {
  const timeTotalledMilliseconds: number = new Date().getTime() + getMillisecondsFrom(numSeconds);

  return new Date(timeTotalledMilliseconds);
}

export function nextDayDate(dayIndex: WeekDay): Date {
  const today: Date = new Date();
  const sevenDaysOfWeekWrap: number = 7;
  today.setDate(today.getDate() + ((dayIndex - 1 - today.getDay() + sevenDaysOfWeekWrap) % sevenDaysOfWeekWrap) + 1);
  const nextDate: Date = today;

  return nextDate;
}
const yearDays: number = 365.25;
const dayHours: number = 24;
const secondsPerMinute: number = 60;
const hourMinutesAndMinuteSeconds: number = 60;
const secondMilliseconds: number = 1000;
const dayMilliseconds: number =
  dayHours * hourMinutesAndMinuteSeconds * hourMinutesAndMinuteSeconds * secondMilliseconds;
/**
 * Gets days from
 * @param years The number of years (to convert from)
 * @returns days from
 */

export function getDaysFrom(years: number): number {
  return years * yearDays; // 365.25?...
}
/**
 * Gets hours from
 * @param days The number of days (to convert from)
 * @returns hours from
 */

export function getHoursFrom(days: number): number {
  return days * dayHours;
}
/**
 * Gets hours from seconds
 * @param days The number of seconds (to convert from)
 * @returns hours from seconds
 */
export function getHoursFromSeconds(seconds: number): number {
  return getHoursFromMinutes(getMinutesFromSeconds(seconds));
}
/**
 * Gets minutes from
 * @param hours The number of hours (to convert from)
 * @returns minutes from
 */
export function getMinutesFrom(hours: number): number {
  return hours * hourMinutesAndMinuteSeconds;
}
/**
 * Gets seconds from
 * @param minutes The number of minutes (to convert from)
 * @returns seconds from
 */
export function getSecondsFrom(minutes: number): number {
  return minutes * hourMinutesAndMinuteSeconds;
}
/**
 * Gets seconds from days
 * @param minutes The number of days (to convert from)
 * @returns seconds from
 */
export function getSecondsFromDays(days: number): number {
  return getSecondsFrom(getMinutesFrom(getHoursFrom(days)));
}
/**
 * Gets seconds from hours
 * @param minutes The number of hours (to convert from)
 * @returns seconds from
 */
export function getSecondsFromHours(hours: number): number {
  return getSecondsFrom(getMinutesFrom(hours));
}
/**
 * Gets milliseconds from
 * @param seconds The number of seconds (to convert from)
 * @returns milliseconds from
 */
export function getMillisecondsFrom(seconds: number): number {
  const milliseconds: number = seconds * secondMilliseconds;

  return milliseconds;
}
export function getMillisecondsFromDays(daysToMultiply: number = 2): number {
  const milliseconds: number = dayMilliseconds * daysToMultiply;

  return milliseconds;
}
/**
 * Gets days from seconds
 * @param seconds number of seconds.
 * @returns days from seconds
 */
export function getDaysFromSeconds(seconds: number): number {
  const minutes: number = getMinutesFromSeconds(seconds);
  const hours: number = getHoursFromMinutes(minutes);
  const days: number = getDaysFromHours(hours);

  return days;
}
/**
 * Gets days from hours
 * @returns days from hours
 */
export function getDaysFromHours(hours: number): number {
  // - TODO: Could refactor these into generic days from hours from days ... diminishing returns...
  return hours / dayHours;
}
/**
 * Gets hours from minutes
 * @returns hours from minutes
 */
export function getHoursFromMinutes(minutes: number): number {
  return minutes / hourMinutesAndMinuteSeconds;
}
/**
 * Gets minutes from seconds
 * @param seconds secs
 * @returns minutes from seconds
 */
export function getMinutesFromSeconds(seconds: number): number {
  return seconds / hourMinutesAndMinuteSeconds;
}
/**
 * Gets seconds from milliseconds
 * @param milliseconds ms
 * @returns seconds from milliseconds
 */
export function getSecondsFromMilliseconds(milliseconds: number): number {
  return milliseconds / secondMilliseconds;
}

export function nextDayDateWithTime(
  dayOfWeek: WeekDay,
  hh: number = 0,
  mi: number = 0,
  ss: number = 0,
  ms: number = 0
): Date {
  const now: Date = new Date();
  const sevenDaysOfWeekWrap: number = 7;

  now.setDate(now.getDate() + ((dayOfWeek + (sevenDaysOfWeekWrap - now.getDay())) % sevenDaysOfWeekWrap));
  const millisecondsEpochDateWithTime: number = now.setHours(hh, mi, ss, ms);
  // Mutate
  const nextDayDateTime: Date = new Date(millisecondsEpochDateWithTime);

  return nextDayDateTime;
}

/**
 *  Safeguard against ERROR Error: GraphQL error: malformed range literal: "["2020-08-01T23:00:00.000Z", "2020-08-02T00:30:00.000Z]""
 * @param start start date time object to get ISO text from
 * @param end end date time
 * @returns postgres-formatting string through apollo hasura
 */
export function getPostgresTimestamptzTstzRange(start: Date, end: Date): string {
  // -TODO: Refactor
  const startString: string = start.toISOString();
  const endString: string = end.toISOString();
  const quoteLiteralForApolloPostgresHasuraTstzRange: string = `\\"`;
  // Alias
  const qL: string = quoteLiteralForApolloPostgresHasuraTstzRange;
  // "bookingAvailabilityDateTimeRange": "[\"2020-07-25 23:00:00+00\",\"2020-07-26 00:30:00+00\"]",
  // ? Incorrect format not working "["2020-07-25 23:00:00+00","2020-07-26 00:30:00+00"]""
  // Test if Hasura requires escape characters. Or custom handling.
  const timerangeString: string = `[${qL}${startString}${qL}, ${qL}${endString}${qL}]`;

  return timerangeString;
}
