import gql from "graphql-tag";
import { filter, map } from "rxjs/operators";
import { getMillisecondsFromDays } from "src/app/shared/utilities/date-utilities";
import {
  Booking_constraint,
  Booking_update_column,
  Event_constraint,
  Event_update_column,
  PersonBooking_constraint,
  PersonBooking_update_column,
  User_obj_rel_insert_input
} from "__generated__/globalTypes";
import {
  Event_obj_rel_insert_input,
  Organiser_constraint,
  Organiser_obj_rel_insert_input,
  Organiser_update_column,
  PersonBooking_arr_rel_insert_input,
  User_constraint,
  User_update_column
} from "./../../../../../__generated__/globalTypes";
import { Person } from "./../models/person-form";
import {
  AddUpsertPersonToSimpleBooking,
  AddUpsertPersonToSimpleBookingVariables
} from "./__generated__/AddUpsertPersonToSimpleBooking";
import {
  GetCountPersonBookingsForEvent,
  GetCountPersonBookingsForEventVariables
} from "./__generated__/GetCountPersonBookingsForEvent";
import { GetPeopleBookedEvents } from "./__generated__/GetPeopleBookedEvents";

export const PLACEHOLDER_EVENT_UUID: string = "667c1a54-3c59-4eff-a507-b55a80094249";

export const NOBODY_DEFAULT_NAME_TEXT: string = "nobody";

export const NOBODY_EMAIL: string = `${NOBODY_DEFAULT_NAME_TEXT}@singletouchdigital.com.au`;

export const NOBODY_MAGIC_CODE: string = NOBODY_DEFAULT_NAME_TEXT;

const GET_PEOPLE_BOOKED_EVENTS: DocumentNode = gql`
  subscription GetPeopleBookedEvents {
    Person {
      personFirstName
      randomUUID
      PersonBookings {
        randomUUID
        checkInDateTime
        checkOutDateTime
        magicCode
        Booking {
          randomUUID
          Event {
            randomUUID
            eventName
            eventStartDateTime
            eventEndDateTime
            eventCapacity
            eventCategory
            eventDateTimeRange
          }
        }
      }
    }
  }
`;

const GET_EVENTS_BOOKED_PEOPLE: DocumentNode = gql`
  subscription GetEventsBookedPeople {
    Event {
      randomUUID
      eventName
      eventStartDateTime
      eventEndDateTime
      eventDescription
      eventCapacity
      Bookings {
        PersonBookings {
          Person {
            randomUUID
            personFirstName
          }
          Booking {
            randomUUID
          }
        }
      }
    }
  }
`;

const GET_ALL_PERSONS_BOOKED: DocumentNode = gql`
  subscription GetPersonBookings {
    PersonBooking {
      Person {
        randomUUID
        personFirstName
      }
      randomUUID
      checkInDateTime
      checkOutDateTime
      Booking {
        randomUUID
        Event {
          eventName
          eventStartDateTime
          eventEndDateTime
        }
      }
    }
  }
`;

const ADD_PERSON_SIMPLE_BOOKING: DocumentNode = gql`
  mutation AddUpsertPersonToSimpleBooking($personBookingUpsertObject: [Person_insert_input!]!) {
    insert_Person(objects: $personBookingUpsertObject) {
      affected_rows
      returning {
        randomUUID
        personFirstName
        PersonBookings {
          randomUUID
          Booking {
            randomUUID
            Event {
              randomUUID
              eventName
            }
          }
        }
      }
    }
  }
`;

const GET_COUNT_PERSON_BOOKING_FOR_EVENT: DocumentNode = gql`
  # Number of people (primary + secondary). Rather than primary user bookings.
  subscription GetCountPersonBookingsForEvent($eventUUID: uuid = "") {
    PersonBooking_aggregate(where: { Booking: { Event: { randomUUID: { _eq: $eventUUID } } } }) {
      aggregate {
        count
      }
    }
  }
`;
@Injectable({
  providedIn: "root"
})
export class BookingService {
  constructor(public apollo: Apollo) {}

  /**
   * The total number of people attached to all bookings for this event
   * Note: Includes secondary people. Not only the primary user who made the booking.
   *
   * The primary user is expected to also be the first PersonBooking made under their user booking.
   * @param eventUUID event being booked by people
   */
  getNumberOfPersonBookingsForEvent(
    eventUUID: string = PLACEHOLDER_EVENT_UUID,
    apollo: Apollo = this.apollo
  ): Observable<FetchResult<GetCountPersonBookingsForEvent>> {
    const options: SubscriptionOptions<GetCountPersonBookingsForEventVariables> = {
      query: GET_COUNT_PERSON_BOOKING_FOR_EVENT,
      fetchPolicy: "cache-first",
      variables: { eventUUID }
    };

    const eventPersonsCount: Observable<FetchResult<GetCountPersonBookingsForEvent>> = apollo.subscribe<
      GetCountPersonBookingsForEvent,
      GetCountPersonBookingsForEventVariables
    >(options);

    return eventPersonsCount;
  }

  getPeopleBookedEvents(apollo: Apollo = this.apollo): Observable<FetchResult<GetPeopleBookedEvents>> {
    const options: SubscriptionOptions = {
      query: GET_PEOPLE_BOOKED_EVENTS,
      fetchPolicy: "cache-first"

    };
    const getPeopleBookingEvents: Observable<FetchResult<GetPeopleBookedEvents>> = apollo.subscribe<GetPeopleBookedEvents>(
      options
    );

    return getPeopleBookingEvents;
  }

  getPersonsFromBookingUUID(bookingUUID: string, apollo: Apollo = this.apollo) {}

  addPersonToBookingByUUID(
    bookingUUID: string,
    { personFirstName, personLastName, personPhoneNumber, personEmailAddress, randomUUID }: Person,
    apollo: Apollo = this.apollo
  ): Observable<FetchResult<AddUpsertPersonToSimpleBooking>> {
    const variables: AddUpsertPersonToSimpleBookingVariables = {
      personBookingUpsertObject: [
        {
          personFirstName,
          personLastName,
          personPhoneNumber,
          personEmailAddress,
          randomUUID,
          PersonBookings: {
            data: [
              {
                Booking: {
                  data: { randomUUID: bookingUUID },
                  on_conflict: {
                    constraint: Booking_constraint.Booking_randomUUID_key,
                    update_columns: [Booking_update_column.updated_at]
                  }
                }
              }
            ],
            on_conflict: {
              constraint: PersonBooking_constraint.PersonBooking_randomUUID_key,
              update_columns: [PersonBooking_update_column.updated_at]
            }
          }
        }
      ]
    };

    const addPersonReturning: Observable<FetchResult<AddUpsertPersonToSimpleBooking>> = apollo.mutate<
      AddUpsertPersonToSimpleBooking,
      AddUpsertPersonToSimpleBookingVariables
    >({
      mutation: ADD_PERSON_SIMPLE_BOOKING,
      variables
    });

    return addPersonReturning;
  }

  addPersonToSimpleBookingEvent(
    { personFirstName, personLastName, personPhoneNumber, personEmailAddress, randomUUID }: Person,
    apollo: Apollo = this.apollo
  ): Observable<FetchResult<AddUpsertPersonToSimpleBooking>> {
    const PersonBookings: PersonBooking_arr_rel_insert_input = this.getPersonBookingsDefaultedPlacehoilder();
    const variables: AddUpsertPersonToSimpleBookingVariables = {
      personBookingUpsertObject: [
        {
          personFirstName,
          personLastName,
          personPhoneNumber,
          personEmailAddress,
          randomUUID,
          PersonBookings: {
            data: [
              {
                Booking: {
                  data: {
                    User: {
                      // - FIXME: Default.
                      data: { username: "bookee", emailAddress: "info@singletouchdigital.com.au" },
                      on_conflict: {
                        constraint: User_constraint.User_username_key,
                        update_columns: [User_update_column.updated_at]
                      }
                    }
                  },
                  on_conflict: {
                    constraint: Booking_constraint.Booking_randomUUID_key,
                    update_columns: [Booking_update_column.updated_at]
                  }
                }
              }
            ],
            on_conflict: {
              constraint: PersonBooking_constraint.PersonBooking_randomUUID_key,
              update_columns: [PersonBooking_update_column.updated_at]
            }
          }
        }
      ]
    };
    const addPersonReturning: Observable<FetchResult<AddUpsertPersonToSimpleBooking>> = apollo.mutate<
      AddUpsertPersonToSimpleBooking,
      AddUpsertPersonToSimpleBookingVariables
    >({
      mutation: ADD_PERSON_SIMPLE_BOOKING,
      variables
    });

    return addPersonReturning;
  }

  getPersonBookingsDefaultedPlacehoilder(
    Event: Event_obj_rel_insert_input = this.getEventPlaceholder()
  ): PersonBooking_arr_rel_insert_input {
    return {
      data: [
        {
          magicCode: NOBODY_MAGIC_CODE,
          Booking: {
            data: {
              Event,
              User: this.getUserPlaceholder(),
              magicCode: NOBODY_MAGIC_CODE
            },
            on_conflict: {
              constraint: Booking_constraint.Booking_randomUUID_key,
              update_columns: [Booking_update_column.updated_at]
            }
          }
        }
      ],
      on_conflict: {
        constraint: PersonBooking_constraint.PersonBooking_randomUUID_key,
        update_columns: [PersonBooking_update_column.updated_at]
      }
    };
  }

  getEventPlaceholder(
    placeholderEventUUID: string = PLACEHOLDER_EVENT_UUID,
    nobodyDefaultNameText: string = NOBODY_DEFAULT_NAME_TEXT,
    nobodyEmail: string = NOBODY_EMAIL,
    Organiser: Organiser_obj_rel_insert_input = this.getOrganiserPlaceholder(nobodyDefaultNameText, nobodyEmail)
  ): Event_obj_rel_insert_input {
    const baseDateNow: Date = new Date();
    const bookingAvailableStartDateTime: Date = new Date(baseDateNow.getTime() - getMillisecondsFromDays(2));
    const bookingAvailableEndDateTime: Date = new Date(baseDateNow.getTime() + getMillisecondsFromDays(7));
    const eventStartDateTime: Date = new Date(baseDateNow.getTime() + getMillisecondsFromDays(9));
    const eventEndDateTime: Date = new Date(baseDateNow.getTime() + getMillisecondsFromDays(10));
    const endDate: Date = new Date(baseDateNow.getTime() + getMillisecondsFromDays(10));

    const startTimeRange = "2020-07-22T09:27:53.667Z";
    const endTimeRange = "2020-08-14T09:27:53.667Z";
    const placeholderDateTimeRange: string = `('${startTimeRange}', '${endTimeRange}']`;

    return {
      data: {
        randomUUID: placeholderEventUUID,
        bookingAvailableStartDateTime,
        bookingAvailableEndDateTime,
        eventStartDateTime,
        eventEndDateTime,
        bookingAvailabilityDateTimeRange: placeholderDateTimeRange,
        eventDateTimeRange: placeholderDateTimeRange,
        eventName: "Sunday service",
        eventTopic: "Topic ",
        eventMedium: "In-person",
        eventCategory: "ChurchCategory",
        eventCapacity: 49,
        eventDescription: "Description of community event",
        Organiser
      },
      on_conflict: {
        constraint: Event_constraint.Event_randomUUID_key,
        update_columns: [Event_update_column.updated_at]
      }
    };
  }

  getOrganiserPlaceholder(
    nobodyDefaultNameText: string = NOBODY_DEFAULT_NAME_TEXT,
    nobodyEmail: string = NOBODY_EMAIL
  ): Organiser_obj_rel_insert_input {
    return {
      data: {
        organiserDetails: nobodyDefaultNameText,
        User: this.getUserPlaceholder()
      },
      on_conflict: {
        constraint: Organiser_constraint.Organiser_randomUUID_key,
        update_columns: [Organiser_update_column.updated_at]
      }
    };
  }

  getUserPlaceholder(
    nobodyDefaultNameText: string = NOBODY_DEFAULT_NAME_TEXT,
    nobodyEmail: string = NOBODY_EMAIL
  ): User_obj_rel_insert_input {
    return {
      data: {
        username: nobodyDefaultNameText,
        emailAddress: nobodyEmail
      },
      on_conflict: {
        constraint: User_constraint.User_username_key,
        update_columns: [User_update_column.updated_at]
      }
    };
  }
}

export function getLatestPersonCountFromQuerySubscription(
  queryPersonCountForEventRealTime: Observable<FetchResult<GetCountPersonBookingsForEvent>>
): Observable<number> {
  return queryPersonCountForEventRealTime.pipe(
    // - FIXME: This seems to consume without passing through... /* errors?.length === 0 */
    filter(({ errors }: FetchResult<GetCountPersonBookingsForEvent>) => errors === undefined || errors.length === 0),
    map(
      ({
        data: {
          PersonBooking_aggregate: { aggregate: numberOfPeopleInEvent }
        }
      }: FetchResult<GetCountPersonBookingsForEvent>) => numberOfPeopleInEvent.count
    )
  );
}
